html,
body {
    position: absolute;
    font-family: "Noto Sans TC", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    top: 0;
    left: 0;
    margin: 0;
    width: 100%; /* don't use 100vw */
    background-color: #fafafa;
}

.main {
    min-height: 100vh;
}

a:visited,
a:link {
    color: inherit;
}

a {
    text-decoration: none;
}

.icon {
    text-align: center;

    width: 1.5em;
    padding: 0.1em;
}

/* Home-nav */
#home-nav {
    position: fixed;
    display: block;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background-color: rgba(255, 255, 255, 0.7);

    top: 0;
    left: 0.7em;
    height: 2.4em;
    line-height: 2.4em;
    font-size: 1.3em;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    z-index: 5;
}

#home-nav a {
    text-decoration: none;
    display: block;

    margin: 0;
}

#home-nav a:hover {
    color: lightseagreen;

    transition: 0.2s;
}

#home-nav .btn:hover {
    color: lightseagreen;
    transition: 0.2s;
}

#home-nav ul {
    list-style-type: none;

    margin: 0;
    padding: 0;
    height: inherit;
}

#home-nav li {
    float: left;
    text-align: center;
    overflow: hidden;

    width: 2.4em;
    height: inherit;
    transition: 0.35s;
}

#home-nav .hide {
    visibility: hidden;

    width: 0;
}

#home-nav .show {
    display: block !important;
}

#home-nav .active {
    background: #aaa9;
}

#music-player {
    position: absolute;
    visibility: hidden;
    transform: translateX(-350px);

    z-index: 3;
    opacity: 0.5;
    margin-top: 0.1em;
    height: 80px;
    transition: 0.35s;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

#music-player:hover {
    opacity: 1;
}

#music-player.show {
    visibility: visible;
    transform: translateX(0px);
}

/* Side-nav */
#side-nav {
    background-color: #18a5a5;
    position: fixed;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;

    left: -11em;
    top: 0;
    width: 11em;
    height: 100%;
    font-size: 1.3em;
    transition: transform 0.15s;
    transition-delay: 0.1s;
    z-index: 10;
}

.show-sidenav {
    transform: translate3d(11em, 0, 0);
}

#side-nav a {
    display: flex;
    color: white;
    text-align: left;
    align-items: center;
    text-decoration: none;
    box-sizing: border-box;

    left: 0;
    width: 100%;
    height: 2em;
    padding: 0 0.5em;
}

#side-nav .sidenav-btn-wrapper {
    position: absolute;
    text-align: center;

    left: 0.7em;
    z-index: 5;
    width: 2.4em;
    height: 2.4em;
    color: white;
    line-height: 2.4em;
}

#side-nav a:hover {
    background-color: #89c9c96c;
    box-shadow: 0 0 28px rgba(0, 0, 0, 0.25), 0 0 10px rgba(0, 0, 0, 0.22);
    transition: 0.1s;
}

#side-nav .icon {
    margin-right: 0.2em;
}

#side-nav ul {
    list-style-type: none;

    margin: 0;
    padding: 10px;
}

#side-nav .category {
    overflow: auto;

    width: 100%;
    height: 100%;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

#side-nav .profile {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background: #2ca8c7;
    background-image: radial-gradient(circle, #5fd4f1, #1c9bbb);

    height: 10em;
    width: 100%;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

#side-nav .profile img {
    display: block;
    position: relative;

    width: 45%;
    border-radius: 50%;
}

#side-nav .profile span {
    text-align: center;
    position: relative;
    display: block;
    -webkit-user-select: text;
        -ms-user-select: text;
            user-select: text;
    color: white;
}

#side-nav #profile-img:hover {
    animation: heartbeat 0.5s forwards;
}

#side-nav #profile-quote {
    font-size: 13px;
}

#side-nav #profile-name {
    font-size: 30px;
}

/* Others */
.btn {
    cursor: pointer;
}

.ripple {
    position: relative;
}

/* Animation effect */
.spin-effect {
    animation: spin 0.5s ease-out forwards;
}

.ripple-effect {
    position: absolute;
    overflow: hidden;

    top: 0;
    left: 0;
    width: 2.4em;
    height: 2.4em;
    opacity: 1;
    border-radius: 50%;
    transform: scale(0);
    background: rgba(0, 0, 0, 0.15);
    animation: ripple 0.3s ease-out forwards;
}

@media only screen and (max-width: 700px) {
    #home-nav a,
    #home-nav li {
        display: none;
    }
}

/* Animation */
@keyframes ripple {
    100% {
        transform: scale(1.3);
    }
}

@keyframes heartbeat {
    50% {
        transform: scale(0.7);
    }
    75% {
        transform: scale(1.1);
    }
    0%,
    100% {
        transform: scale(1);
    }
}

@keyframes stretch {
    from {
        width: 0;
    }
    to {
        width: 200px;
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

