#footer {
    position: absolute;
    background: #ddd6;
    text-align: center;
    color: black;
    box-sizing: border-box;

    width: 100%;
    padding: 0.7em;
    transition: 0.3s;
}

#footer:hover {
    background: #454545;
    color: white;
}
 
.section {
    padding: 0.6em 2.5em;
}

.section > h2 {
