/* Introduction */
#intro {
    display: flex;
}

#intro .bio {
    width: 55vw;
    margin-left: 1.5em;
    padding-left: 1.5em;
    border-left: solid #cccc;
}

#intro .bio .info {
    display: flex;
    flex-direction: column;

    font-size: 0.9em;
}

#intro .bio a {
    text-decoration: none;
}

#intro .bio a:hover {
    color: #3fc1c9;
}

#intro .bio .paragraph {
    padding: 1em 0;
}

/* Education */
#intro .education {
    width: 40vw;
}

#intro .timeline-container {
    display: flex;
    overflow: hidden;
    box-sizing: border-box;
    justify-content: center;

    min-height: 15em;
}

#intro .edu-timeline {
    position: absolute;

    width: 35vw;
    max-width: 25em;
}

#intro .edu-timeline::after {
    content: "";
    position: absolute;
    box-sizing: border-box;
    background-color: white;

    top: 0;
    bottom: 0;
    width: 0.6em;
    margin-left: -0.55em;
    border: solid 1px #cccc;
    border-radius: 0.2em;
}

#intro .edu-timeline .container {
    position: relative;

    padding: 0 0 1em 1.5em;
}

#intro .edu-timeline .container::after {
    content: "";
    position: absolute;
    background-color: white;

    width: 1.6em;
    height: 1.6em;
    z-index: 1;
    border-radius: 50%;
    top: 0;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

#intro .edu-timeline .before::after {
    /* 1.1 = circle_radius(1.6 / 2) + line_width_half(0.6 / 2)  */
    left: -1.1em;
}

#intro .edu-timeline .current::after {
    /* 1.3 = circle_radius(1.6 / 2) + border_size(0.2) + line_width_half(0.6 / 2)  */
    left: -1.3em;
    border: 0.2em solid #ff5e5f;
}

#intro .edu-timeline .content {
    position: relative;
    text-align: center;
    background-color: white;

    padding: 0.1em;
    border-radius: 0.5em;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

#intro .edu-timeline .school {
    display: flex;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    align-items: center;
    justify-content: center;

    color: #1b262c;
    font-size: 1.2em;
    transition: 0.1s;
}

#intro .edu-timeline .school > img {
    height: 0;
    width: auto;
    margin: 0.2em 0.2em 0.2em 0;
}

#intro .edu-timeline .major {
    display: block;
    color: gray;
    display: none;

    font-size: 0.9em;
}

#intro .edu-timeline .school:hover + .major {
    display: block !important;
}

#intro .edu-timeline .school:hover {
    color: black !important;

    font-size: 1.3em;
    cursor: default;
}

#intro .edu-timeline .school:hover img {
    height: 1.25em;
}

/* Experiences */
#exp > .content {
