    margin-bottom: 0.5em;
}

#projects .links a {
    margin-right: 0.3em;
}

#projects .links a:hover {
    color: #3fc1c9;
}

#projects .tags {
    border-left: 0.1em solid #aaa;
}

#projects .tags span {
    cursor: pointer;

