/* My Publications */
#publications > .content {
    display: flex;
    flex-direction: column;

    margin-bottom: 1em;
}

#publications > .content a {
    text-decoration: none;

    padding: 0.25em 0.5em;
}

#publications > .content a:hover {
    background: #0001;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 1px 2px, rgba(0, 0, 0, 0.2) 0px 1px 2px;
}

@media only screen and (max-width: 900px) {
    /* header */
    h2.header {
        text-align: center;
    }

    /* profile */
    #profile > .header {
        flex-wrap: wrap;
    }
    #profile .profile-img {
        width: 100vw;
        justify-content: center;
    }
    #profile .basic-info {
        width: 100vw;
        justify-content: center;
    }

    /* Introduction */
    #intro {
        flex-wrap: wrap;
    }

    #intro .education {
        width: 100vw;
    }

    #intro .timeline-container .edu-timeline {
        width: 70vw;
    }

    #intro .bio {
        text-align: center;

        margin: 0;
        padding: 0;
        border: none;
        width: 100vw;
    }

    /* Skills */
    #skills > .content {
        flex-wrap: wrap-reverse;
    }

    #skills .details {
