@media only screen and (max-width: 450px) {
    #intro .edu-timeline {
        font-size: 0.85em;
    }
}

/* header */
#header {
    position: relative;
    display: flex;
    overflow: hidden;
    justify-content: center;
    align-items: center;
    background: #74ebd5;
    background: linear-gradient(to bottom right, #74ebd5, #acb6e5);

    top: 0;
    left: 0;
    width: 100%;
    height: calc(100vh - 2.5em);
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
}

.bg-bubbles {
    position: absolute;

    top: 0;
    left: 0;
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
}

.bg-bubbles li {
    position: absolute;
    list-style: none;
    display: flex;
    background: white;

    width: 50px;
    height: 50px;
    bottom: -200px;

    animation: bubble 25s ease-out infinite;
}

/* title-box */
#title-box {
    display: flex;
    flex-direction: column;
    font-family: "Roboto", sans-serif;
    align-items: center;
    color: #333b;

    font-size: 4.5em;
    z-index: 1;
}

#title-box .title {
    display: flex;
    flex-direction: column;
    background: #fffc;
    transform-origin: top left;

    padding: 0 0.2em;
    margin: 0.2em;
    border-radius: 0.1em;
}

#title-box span:nth-child(1) {
