/* Animation */
@keyframes bubble {
    from {
        opacity: 0.3;
        transform: translateY(0) rotate(0) scale(0.7);
    }
    to {
        opacity: 0;
        transform: translateY(-100vh) rotate(360deg) scale(1.3);
    }
}

@keyframes swipe-up {
    0% {
        transform: translateY(0.5em);
    }
    1%,
    10%,
    51%,
    60% {
        transform: translateY(-0.5em);
    }
    11%,
    20%,
    61%,
    70% {
        transform: translateY(-2.5em);
    }
    21%,
    30%,
    71%,
    80% {
        transform: translateY(-4.5em);
    }
    31%,
    40%,
    81%,
    90% {
        transform: translateY(-6.5em);
    }
    41%,
    50%,
    91%,
    100% {
        transform: translateY(-8.5em);
    }
}

@keyframes zoom {
    from {
        opacity: 0;
        transform: scale(0);
    }
    to {
        opacity: 1;
        transform: scale(1);
    }
}

/* Resposive Web Design */
@media screen and (max-width: 800px) {
    #title-box {
        font-size: 3.5em;
    }

    #quick-nav > a:first-child {
        display: none;
    }
}

#project {
    background: repeating-linear-gradient(
            45deg,
            #2b2b2b2b,
            #2b2b2b2b 1px,
            transparent 1px,
            transparent 7px
        ),
        linear-gradient(to bottom, #355c7d 50%, #6c5b7b, #c06c84);

    background-repeat: no-repeat;
    background-attachment: fixed;
}

/* Header */
#project .header {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    font-family: "Monoton", cursive;
    color: white;

    height: 12em;
    width: 100%;
}

#project .header a {
    color: white;
    text-decoration: none;
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #fff, 0 0 20px #ff1177,
        0 0 35px #ff1177, 0 0 40px #ff1177, 0 0 50px #ff1177, 0 0 75px #ff1177;

    font-size: calc(100vw / 10);
    transition-duration: 0.05s;
}

#project .header a:hover {
    color: #ff1177;
    text-shadow: none;
}

/* Project Articles */
#project .projects-list {
    padding: 1em;
}

#project .projects-list a,
#project .projects-list a:visited {
    display: flex;
    margin: 0.3em auto;
    text-decoration: none;
    background: #2b2b2b88;
    color: white;
    align-items: center;

    border-radius: 0.3em;
    width: 60vw;
    font-size: 1.3em;
    padding: 10px;
    border: solid 1px #2b2b2b88;
}

#project .projects-list a:hover {
    background: #2b2b2b;
    border: solid 1px white;
}

#project .projects-list .project-date {
    text-align: center;
    border-right: 0.1em solid;

    font-size: 0.8em;
    line-height: 1em;
    width: 4em;
    opacity: 0.8;
}

#project .project-date .year {
    color: gray;

    font-size: 0.7em;
}

#project .project-date .month-day {
    font-size: 1em;
}

#project .projects-list .project-title {
    margin: 0 10px;
}

/* Resposive Web Design */
@media screen and (max-width: 500px) {
    #project .projects-list a {
        width: 80vw;
    }
}

#article {
    padding: 2em;
    background: #292b36;
}

/* Header */
#article .header {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    font-family: "Ubuntu Mono", monospace;

    height: 50vh;
    color: white;
}

#article .header .title-box {
    position: fixed;
    text-align: center;

    padding: 1em;
    margin-top: 1em;
}

#article .header .title {
    display: flex;
    margin: auto;

    font-size: calc(100vw / 15);
}

#article .header .date {
    font-weight: 300;
    padding-top: 1em;
}

#article .typing-animation {
    overflow: hidden;
    white-space: nowrap;

    width: 0;
    border-right: 0.1em solid #e7e8eb;
    animation: typing 2s steps(22, end) 0.5s forwards,
        blinking-cursor 0.5s step-end infinite;
}

/* content */
#article .content {
    min-height: 50vh;
    position: relative;

    background: #fafafa;
    border-radius: 0.3em;
    padding: 0.5em 1em;
    z-index: 1;
}

#article .article-block {
    padding: 0.5em 1em;
    margin-top: 0.1em;
    border-radius: 0.3em;
}

#article .article-block:hover {
    background: #292b3633;
}

#article .article-block .title-date {
    display: flex;
    align-items: center;
}

#article .article-block .title {
    font-size: 1.5em;
    margin-bottom: 0.2em;
}

#article .article-block .date {
    font-size: 0.8em;
    margin-left: auto;
}

#article .article-block .preview {
    color: #555;
    font-weight: 300;
}

/* article content */
#article .article {
    font-size: 1.2em;
}

#article .article a:hover {
    color: lightseagreen;
    transition: 0.2s;
}

/* google slides */
#article .google-slides iframe {
    border: 0;
    position: relative;
    top: 0;
    left: 0;
    width: calc(70vh * 16 / 9) !important;
    height: 75vh !important;
}

/* Animation */
@keyframes typing {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}

@keyframes blinking-cursor {
    0% {
        border-color: transparent;
    }
    50% {
        border-color: #e7e8eb;
    }
}

